<template>
  <section class="tomato_section">
    <header class="tomato_header">우리 토마토</header>
    <apexchart width="500" height="500" :options="chartOptions" :series="series" class="toamato_chart"></apexchart>
  </section>
</template>

<script>
import '@/assets/style/DetailStudy/tomato_rate.scss'
import {useStore} from 'vuex'
import { computed, reactive } from 'vue'

export default {
  name: 'TomatoRate',


  setup() {
    const store = useStore()
    const state = reactive({
      total_sum : computed(() => {
        return store.state.studyTomato
      }),
      study_tomato : computed(() => {
        return store.state.totalTomato
      })

    })

    const series =  [state.total_sum/state.study_tomato]
    console.log(state.total_sum, state.study_tomato)
    
    const chartOptions = {
      chart: {
        height: 350,
        type: 'radialBar',
      },
      plotOptions: {
        radialBar: {
          // 그래프 시작점, 끝점 지정하기
          // startAngle: 0,
          // endAngle: 225,
            hollow: {
            margin: 0,
            size: '70%',  // 원 둘레 두께
            // background: '#293450',   // 원 안쪽 색깔
            image: 'data:image/jpeg;base64,',
            imageWidth: 64,
            imageHeight: 64,
            imageClipped: true,
            position: 'front',
            // 그림자 기술 넣기
            dropShadow: {
              enabled: true,
              top: 3,
              left: 0,
              blur: 4,
              opacity: 0.24
            }
          },
          track: {
            background: '#fff',  // 원 둘레 중 안채워진 공간 색깔
            strokeWidth: '70%',
            margin: 0, // margin is in pixels
            dropShadow: {
              enabled: true,
              top: -3,
              left: 0,
              blur: 4,
              opacity: 0.35
            }
          },
      
          dataLabels: {
            show: true,
            name: {   // 여기 더 파보기 !!!!!!
              offsetY: -10,
              show: true,
              color: '#fff',
              fontSize: '17px'
            },
            value: {
              color: '#fff',
              fontSize: '36px',
              show: true,
            }
          }
        }
      },
      fill: {
        type: 'gradient',
        gradient: {
          shade: 'dark',
          // type: 'horizontal',
          type: 'vertical',
          shadeIntensity: 0.5,
          gradientToColors: ['#ABE5A1'],  // 원 둘레의 색깔을 변경 가능하다
          inverseColors: true,
          opacityFrom: 1,
          opacityTo: 1,
          stops: [0, 100]
        }
      },
      stroke: {
        lineCap: 'round'
      },
      labels: [''],  // 원 안에 내용 원하는거 작성하세요
    }

    

    return {
      state,
      chartOptions,
      series,

    }
  }

}
</script>

